// Import các module cần thiết từ MUI
import { createTheme } from "@mui/material/styles";

// Tạo theme
const theme = createTheme({
  // Cấu hình Typography (font chữ)
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif', // Cấu hình font mặc định
    fontSize: 12, // Kích thước font mặc định
    h1: {
      fontSize: "2.2rem",
    },
    h2: {
      fontSize: "2rem",
    },
    h3: {
      fontSize: "1.8rem",
    },
    h4: {
      fontSize: "1.6rem",
    },
    h5: {
      fontSize: "1.4rem",
    },
    h6: {
      fontSize: "1.2rem",
    },
  },

  // Cấu hình Palette (màu sắc)
  palette: {
    primary: {
      main: "#1976d2", // Màu chính của ứng dụng
    },
    secondary: {
      main: "#dc004e", // Màu phụ của ứng dụng
    },
    background: {
      default: "#fafafa", // Màu nền mặc định
      paper: "#ffffff", // Màu nền cho các thành phần như Card
    },
    text: {
      primary: "#333333", // Màu chữ chính
      secondary: "#777777", // Màu chữ phụ
    },
  },

  // Cấu hình các thành phần (components)
  components: {
    // Thay đổi kiểu button
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: "none", // Không chuyển chữ thành in hoa
          borderRadius: 4, // Bo tròn góc cho button
        },
      },
    },

    // Thay đổi kiểu TextField
    MuiTextField: {
      styleOverrides: {
        root: {
          margin: "10px 0", // Khoảng cách giữa các TextField
        },
      },
    },

    // Thay đổi kiểu Card
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: 8, // Bo tròn góc cho Card
        },
      },
    },
  },

  // Cấu hình các breakpoints (cho responsive design)
  breakpoints: {
    values: {
      xs: 0, // Dưới 600px
      sm: 600, // Từ 600px
      md: 900, // Từ 900px
      lg: 1200, // Từ 1200px
      xl: 1536, // Từ 1536px
    },
  },

  // Cấu hình spacing
  spacing: 4, // Khoảng cách mặc định cho layout

  // Cấu hình shape
  shape: {
    borderRadius: 8, // Bo tròn góc mặc định cho các thành phần
  },
});

export default theme;
